.product-box {
    border: 1px solid $color-blue-medium;
    background-color: #d7e9f7;
    border-radius: 0 0 3px 3px;

    &--rounded-top {
        border-radius: 3px;
    }

    &__row {
        display: flex;
        justify-content: center;
        transition: 0.15s all ease-out;
        height: 4rem;
        align-items: center;
        background-color: white;

        &:not(:first-child) {
            border-radius: 3px;
            border-bottom: 1px solid #ddd;
        }

        &--header {
            background-color: $color-blue-medium !important;
            color: white;
            height: 5rem;
        }

        &:hover {
            background-color: #eee;
        }
    }

    &__col {
        flex-grow: 1;
        flex-basis: 50rem;
        padding: 0.5rem 0 0.5rem 2rem;
        height: 100%;
        display: flex;
        align-items: center;

        &:not(:last-child) {
            border-right: 1px solid #ddd;
        }

        &--header {
            &:not(:last-child) {
                border-right: 1px solid $color-blue-col-delimiter;
            }
        }
    }
}
